import React, { useEffect, useState } from "react";
import wificonnected from "./images/wifi.jpg";
import wifidisconnected from "./images/wifi_diconnected.png";
import "./styles/sidebar.css";

function Sidebar() {
  const [wificonnect, setwificonnect] = useState(true);
  useEffect(() => {
    if (wificonnect === false) {
      document.getElementById(
        "wifi"
      ).style.backgroundImage = `url(${wifidisconnected})`;
      document.getElementById(
        "wifi-btn"
      ).style.backgroundImage = `url(${wifidisconnected})`;
      document.getElementById("connected").innerHTML = "Disconnected";
    }
    if (wificonnect === true) {
      document.getElementById(
        "wifi"
      ).style.backgroundImage = `url(${wificonnected})`;
      document.getElementById(
        "wifi-btn"
      ).style.backgroundImage = `url(${wificonnected})`;
      document.getElementById("connected").innerHTML = "Connected";
    }
  }, [wificonnect]);

  return (
    <div style={{ height: "94%", width: "350px" }} id="sidebar">
      <div className="sidebar-bottom">
        <div className="brightness">
          <div className="light-icon" id="light-icon"></div>
          <input
            type="range"
            name="volume"
            id="volume"
            className="light"
            max="100"
            min="0"
            defaultValue="75"
          />
        </div>
        <div className="sound">
          <div className="sound-icon" id="sound-icon"></div>
          <input
            type="range"
            name="volume"
            id="volume"
            className="volume"
            max="100"
            min="0"
            defaultValue="75"
          />
        </div>
        <div
          style={{ width: "100%", display: "flex", justifyContent: "center" }}
        >
          <button className="sidebar-btns" id="battery-btn"></button>
          <button
            className="sidebar-btns"
            id="wifi-btn"
            onClick={() => setwificonnect(!wificonnect)}
          ></button>
          <button className="sidebar-btns" id="setting-btn"></button>
        </div>
        <div
          style={{ width: "100%", display: "flex", justifyContent: "center" }}
        >
          <button className="sidebar-btns" id="theme-change"></button>
          <button className="sidebar-btns" id="bluetooth"></button>
          <button className="sidebar-btns" id="airplane"></button>
        </div>
      </div>
    </div>
  );
}

export default Sidebar;
